.naslovna {
  color: var(--dark-grey);
  padding-bottom: 1.25rem;
}
@media (min-width: 50em) {
  .naslovna {
    display: flex;
    gap: 1.25rem;
  }
}
@media (min-width: 50em) {
  .naslovna img {
    width: 50%;
    max-width: 47.625rem;
  }
}
@media (min-width: 50em) {
  .naslovna__caption {
    margin-top: 9.375rem;
  }
}

.newss {
  display: flex;
  flex-direction: column;
  padding: 1.25rem 0;
}
.newss h1 {
  margin: 0 auto 1.25rem;
  color: var(--dark-grey);
}
.newss .news {
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  flex-direction: column;
  gap: 1.25rem;
}
@media (min-width: 50em) {
  .newss .news {
    flex-direction: row;
  }
}
.newss .button {
  position: relative;
  margin: 1.25rem auto 0;
}

.informations {
  margin: 1.875rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 50em) {
  .informations {
    flex-direction: row;
    gap: 1.875rem;
  }
}

.smjerovi {
  padding-bottom: 7.5rem;
}
.smjerovi .mobileSmjerovi {
  display: flex;
  flex-direction: column;
  gap: 1.875rem;
}
@media (min-width: 50em) {
  .smjerovi .mobileSmjerovi {
    display: none;
  }
}
.smjerovi .mobileSmjerovi__box .picture {
  position: relative;
  display: flex;
  height: 15.625rem;
}
.smjerovi .mobileSmjerovi__box .picture .blue {
  width: 50%;
  background-color: var(--dark-blue);
}
.smjerovi .mobileSmjerovi__box .picture .image {
  width: 50%;
  background-color: var(--white);
}
.smjerovi .mobileSmjerovi__box .picture .slika {
  position: absolute;
  inset: 0;
  padding: 1.25rem;
}
.smjerovi .mobileSmjerovi__box .text {
  padding: 1.25rem;
}
.smjerovi .mobileSmjerovi__box .text h2 {
  color: var(--dark-blue);
}
.smjerovi .mobileSmjerovi__box .text p {
  color: #565656;
}
.smjerovi .desktopSmjerovi {
  display: none;
  margin-top: 4.375rem;
  flex-direction: column;
}
@media (min-width: 50em) {
  .smjerovi .desktopSmjerovi {
    display: flex;
  }
}
.smjerovi .desktopSmjerovi__box {
  position: relative;
  display: flex;
  height: 37.5rem;
}
.smjerovi .desktopSmjerovi__box .blue {
  width: 25%;
  background-color: var(--dark-blue);
}
.smjerovi .desktopSmjerovi__box .white {
  position: relative;
  width: 75%;
  background-color: var(--white);
}
.smjerovi .desktopSmjerovi__box .white .crtaGore {
  position: absolute;
  top: 0;
  right: 1.875rem;
  width: 80%;
  height: 2px;
  background-color: var(--dark-blue);
}
.smjerovi .desktopSmjerovi__box .white .crtaDolje {
  position: absolute;
  bottom: 0;
  left: 15%;
  width: 40%;
  height: 2px;
  background-color: var(--dark-blue);
}
.smjerovi .desktopSmjerovi__box .slika {
  position: absolute;
  inset: 0;
  padding: 5rem 1.875rem;
  display: flex;
  align-items: center;
  gap: 6.25rem;
}
.smjerovi .desktopSmjerovi__box .slika img {
  width: 50%;
  max-width: 35.625rem;
}
.smjerovi .desktopSmjerovi__box:nth-child(2) .crtaGore {
  width: 0;
}
.smjerovi .desktopSmjerovi__box:nth-child(2) .crtaDolje {
  left: 50%;
}
.smjerovi .desktopSmjerovi__box:nth-child(3) .crtaGore {
  width: 0;
}
.smjerovi .desktopSmjerovi__box:nth-child(3) .crtaDolje {
  width: 80%;
}

.radovi h2,
.radovi h3 {
  color: var(--dark-grey);
}
.radovi p,
.radovi li {
  color: #494949;
}
.radovi a {
  color: #5AADF9;
}
.radovi__mobile {
  display: flex;
  flex-direction: column;
}
.radovi__mobile img {
  width: 80%;
  margin: auto;
  padding: 1.25rem 0;
}
.radovi__mobile ul {
  padding: 0.625rem 0 1.875rem;
  line-height: 1.25rem;
}
@media (min-width: 50em) {
  .radovi__mobile {
    display: none;
  }
}
.radovi__desktop {
  display: none;
}
@media (min-width: 50em) {
  .radovi__desktop {
    display: block;
  }
}
.radovi__desktop h2 {
  padding-top: 2.5rem;
}
.radovi__desktop p {
  padding: 1.875rem 0;
}
.radovi__desktop .picture {
  display: flex;
  gap: 5rem;
}
.radovi__desktop .picture img {
  width: 50%;
}
.radovi__desktop .picture .txt {
  width: 50%;
}
.radovi__desktop .picture .txt .blueline {
  margin-top: 0.625rem;
  border-left: 3px solid var(--dark-blue);
  padding-left: 0.375rem;
}

.projects {
  padding: 3.75rem 0 5rem;
}
.projects h2 {
  color: var(--dark-grey);
}
.projects p {
  color: #494949;
}
.projects__naslov {
  display: flex;
  flex-direction: column;
  padding-bottom: 1.875rem;
}
.projects__naslov .crta {
  height: 2px;
  background-color: var(--dark-blue);
}
.projects__projs {
  display: flex;
  flex-direction: column;
  gap: 1.875rem;
}

* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

ul {
  list-style: none;
}

img {
  width: 100%;
  height: 100%;
}

.container {
  position: relative;
  margin: 0 auto;
  width: 90%;
  max-width: 100rem;
}

.button {
  padding: 0.625rem 2.5rem;
  align-self: flex-start;
  background-color: var(--dark-blue);
  border: none;
  outline: none;
  color: var(--white);
  border-radius: 8px;
}

footer {
  margin-top: auto;
}

:root {
  --white: #FFFFFF;
  --black: #000000;
  --dark-grey: #2D3E50;
  --dark-blue: #203A72;
  --light-grey-background: #F1F1F1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

h1 {
  font-family: var(--poppins);
  font-size: 2rem;
  font-weight: bold;
}
