  }
}

h2 {
  font-family: var(--poppins);
  font-size: 1.5rem;
  font-weight: bold;
}
@media (min-width: 71.875em) {
  h2 {
    font-size: 2.125rem;
  }
}

h3 {
  font-family: var(--poppins);
  font-size: 1.25rem;
  font-weight: bold;
}
@media (min-width: 71.875em) {
  h3 {
    font-size: 1.5rem;
  }
}

h4 {
  font-family: var(--poppins);
  font-size: 1rem;
  font-weight: bold;
}
@media (min-width: 71.875em) {
  h4 {
    font-size: 1.25rem;
  }
}

h5 {
  font-family: var(--poppins);
  font-size: 0.875rem;
  font-weight: bold;
}
@media (min-width: 71.875em) {
  h5 {
    font-size: 1rem;
  }
}

h6 {
  font-family: var(--poppins);
