}
@media (min-width: 71.875em) {
  .body_text {
    font-size: 1.125rem;
  }
}

.caption {
  font-family: var(--roboto);
  font-size: 0.875rem;
  font-weight: normal;
}
@media (min-width: 50em) {
  .caption {
    font-size: 1rem;
  }
}

.footer_heading {
  font-family: var(--roboto);
  font-size: 1rem;
  font-weight: bold;
