}

@font-face {
  font-family: "Roboto";
  src: url("./font/Roboto/Roboto-Regular.ttf") format("ttf");
  font-weight: normal;
}
@font-face {
  font-family: "Poppins";
  src: url("./font/Poppins/Poppins-Bold.ttf") format("ttf");
  font-weight: bold;
}
:root {
  --roboto: "Roboto";
  --poppins: "Poppins";
}

.header__mobile {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--dark-blue);
  padding: 0.625rem 2.5rem;
}
@media (min-width: 50em) {
  .header__mobile {
    padding: 1.25rem 1.875rem;
  }
}
.header__mobile .logo {
  padding: 0.9375rem;
}
.header__mobile .menu {
  position: relative;
  height: 1.125rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header__mobile .menu span {
  width: 1.5rem;
  height: 0.125rem;
  border-radius: 0.25rem;
  background-color: var(--white);
}
.header__mobile .menu span::before,
.header__mobile .menu span::after {
  content: "";
  position: absolute;
  left: 0;
  width: 1.5rem;
  height: 0.125rem;
  border-radius: 0.25rem;
  background-color: var(--white);
}
.header__mobile .menu span::before {
  top: 0;
}
.header__mobile .menu span::after {
  bottom: 0;
}
@media (min-width: 50em) {
  .header__mobile {
    display: none;
  }
}
.header__desktop {
  display: none;
}
@media (min-width: 50em) {
  .header__desktop {
    display: block;
  }
}
.header__desktop .navUp {
  background-color: var(--dark-blue);
  padding: 0.625rem 1.25rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (min-width: 50em) {
  .header__desktop .navUp {
    padding: 0.9375rem 1.875rem;
  }
}
.header__desktop .navUp a {
  color: var(--white);
}
.header__desktop .navUp__left {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__desktop .navUp__left li + li {
  margin-left: 1.25rem;
}
.header__desktop .navUp__right {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__desktop .navUp__right span {
  width: 1px;
  height: 30px;
  margin: 0 0.9375rem;
  background-color: var(--white);
}
.header__desktop .navUp__right li + li {
  margin-left: 1.25rem;
}
.header__desktop .navDown {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2.5rem 1.875rem;
}
.header__desktop .navDown__logo {
  padding: 10px 15px;
  background-color: var(--dark-blue);
}
.header__desktop .navDown__right {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__desktop .navDown__right a {
  color: var(--dark-grey);
}
.header__desktop .navDown__right li + li {
  margin-left: 1.25rem;
}

.subheader {
